$tile-height: 400px;

.portfolio-items__tile {
    background: none;
    border: 1px solid $lime;
    border-radius: 5px;
    position: relative;
}   
.portfolio-items__tile__thumbnail {
    background-size: cover;
    background-position: center;
    height: $tile-height;
    overflow: hidden;
    width: 100%;
}
.portfolio-items__tile__mask {
    background: $black-translucent;
    bottom: 0;
    height: $tile-height;
    left: 0;
    opacity: 0;
    position: absolute;
    transition: opacity .25s ease-out;
}
.portfolio-items__tile__label {
    background: $lime;
    color: $black;
    bottom: 0;
    color: $navy;
    position: absolute;
}
.portfolio-items__tile__caption {
    font-size: 0.8rem;
    height: 0;
    opacity: 0;
    transition: height .25s ease-out, opacity .25s;
}
.portfolio-items__tile:hover .portfolio-items__tile__caption {
    height: 80px;
    opacity: 1;
}
.portfolio-items__tile:hover .portfolio-items__tile__mask {
    opacity: 1;
}
